/**
 * Container with horizontal padding
 */
.erz-container {
  margin: 0 auto;
  padding: 0 $erz-container-padding-x;
}

/**
 * Limit the container on large screens
  */
.erz-container-limited {
  max-width: $erz-container-max-width;
  margin-left: 0; // Should be left aligned because of the Evento integration
}

/**
 * Add vertical padding
 */
.erz-container-padding-y {
  padding-top: $erz-container-padding-y;
  padding-bottom: $erz-container-padding-y;
}

.cursor-pointer {
  cursor: pointer;
}

.invalid-feedback {
  color: #dc3545;
  font-size: 80%;
  margin-top: 0.25rem;
}

/*
 * There is an issue where changes to $grid-gutter-width in
 * bootstrap.variables.scss are not applied to container paddings (They jsut
 * stay at 1.5rem).
 * As a workaround, we set the padding explicitly here.
 */
.container-fluid {
  padding-right: var(--bs-gutter-x, $grid-gutter-width);
  padding-left: var(--bs-gutter-x, $grid-gutter-width);
}

/**
 * Remove horizontal padding on small screens
 */
@include media-breakpoint-down(sm) {
  .erz-container-padding-x-responsive {
    padding-left: 0;
    padding-right: 0;
  }
}

.row.row-bottom-divider {
  position: relative;
  overflow: visible;
}
.row.row-bottom-divider::after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  border-bottom: 1px solid $table-border-color;
  height: 0;
  overflow: hidden;
  margin-left: ($grid-gutter-width * 0.5);
  margin-right: ($grid-gutter-width * 0.5);
}

@media (max-width: 768px) {
  .full-width-row {
    margin-left:  (-1 * $grid-gutter-width);
    margin-right: (-1 * $grid-gutter-width);
    padding-left:  $grid-gutter-width;
    padding-right: $grid-gutter-width;
  }
  .row.row-bottom-divider::after {
    margin-left: $grid-gutter-width * -0.5;
    margin-right: $grid-gutter-width * -0.5;
  }
}
