@use "sass:math";
@import "node_modules/bootstrap/scss/functions";

/**
 * Bootstrap variables overrides
 */

// disable responsize font sizes
$enable-rfs: false;

// container padding
$container-padding-x: 1.5rem;

//#0dcaf0, #17a2b8

// Colors
$body-bg: rgb(255, 255, 255);
$body-color: rgb(96, 96, 96);
$yellow: rgb(255, 168, 20);
$orange: rgb(239, 124, 0);
$blue: rgb(65, 118, 145);
$teal: rgb(19, 69, 97);
$gray: rgb(138, 138, 138);
$gray-dark: rgb(51, 51, 51);
$cyan: #17a2b8;

$blue: #005189;
$blue-hover: #004272;
$gray-hover: rgb(116, 116, 116);

$present-color: #28a745;
$absent-color: #dc3545;
$unapproved-color: $yellow;

// Font
$font-family-sans-serif: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

// Root Font Size
$root-font-size-scale: 1.0; // relative to the standard 16px font size of a browser
$font-size-base: math.div(1.0, $root-font-size-scale) * 1rem;
$rem-scale: math.div($font-size-base, 1rem);
// Adjust spacings and paddings to compensate for adjusted root-font-scale
$spacer: $font-size-base;
$table-cell-padding-y:    $rem-scale * 0.7 * .5rem;
$table-cell-padding-x:    $rem-scale * 0.7 * .5rem;
$table-cell-padding-y-sm: $rem-scale * 0.7 * .25rem;
$table-cell-padding-x-sm: $rem-scale * 0.7 * .25rem;
$table-cell-padding-y-md: $rem-scale * 0.7 * .25rem;
$table-cell-padding-x-md: $rem-scale * 0.7 * .25rem;

$input-btn-padding-y: $rem-scale * 0.7 * .375rem;
$input-btn-padding-x: $rem-scale * 0.7 * .75rem;

$grid-gutter-width: $rem-scale * 1.5rem;

// Buttons
$input-btn-focus-box-shadow: none;

// Icons
$erz-icon-size: ($font-size-base * 1.4666666);

@import "node_modules/bootstrap/scss/variables";

:root {
    --stellvertretung-primary: #{$blue};
    --stellvertretung-primary-hover: #{$blue-hover};
    --stellvertretung-secondary: #{$gray};
    --stellvertretung-secondary-hover: #{$gray-hover};
}

// Dropdown
$dropdown-link-hover-bg: $gray-200;

// Tables
$table-head-bg: $table-border-color;
$table-head-color: $gray-600;
$table-group-separator-color: $table-border-color;
$table-th-font-weight: normal;

/**
 * Custom Variables
 */

// Custom variables
$erz-container-max-width: 960px;
$erz-container-padding-x: $spacer;
$erz-container-padding-y: $spacer;
$erz-presence-control-entry-min-width: 400px;
