.bg-primary { background-color: var(--stellvertretung-primary) !important; }
.bg-secondary { background-color: var(--stellvertretung-secondary) !important; }

.btn-primary { background-color: var(--stellvertretung-primary) !important; border-color: var(--stellvertretung-primary) !important;}
.btn-secondary { background-color: var(--stellvertretung-secondary) !important;  border-color: var(--stellvertretung-secondary) !important;}

.btn-primary:hover { background-color: var(--stellvertretung-primary-hover) !important; border-color: var(--stellvertretung-primary-hover) !important;}
.btn-secondary:hover { background-color: var(--stellvertretung-secondary-hover) !important;  border-color: var(--stellvertretung-secondary-hover) !important;}

.dropdown-item.active, 
.dropdown-item:active {
    background-color: var(--stellvertretung-primary) !important;
}

.standalone {
    --stellvertretung-primary: #dd0000;
    --stellvertretung-primary-hover: #bb0000;
}
